@import '../../../config/variables';

$navbar-color-black: #1a1a1a;

.navbar {
  height: $header-height;
  background: $color-header;
  border-bottom: 3px solid $navbar-color-black;
  .navbar-nav {
    > li {
      > a,
      > a:focus,
      > a:hover {
        color: #FFF;
        font-weight: 400;
        text-transform: uppercase;
        font-size: .9rem;
        background-color: transparent;
        text-align: center;
        border-bottom: none;
        padding-left: .7rem;
        padding-right: .7rem;
        @media (min-width: $screen-sm-min) {
          font-size: .7rem;
          color: #f4f4f4;
          &:hover {
            color: #FFF;
            background: inherit;
          }
        }
        @media (min-width: $screen-md-min) {
          font-size: .7rem;
        }
      }
    }
  }
}

.navbar-toggle {
  border: 0;
  padding: 7px 0;
  .icon-bar {
    height: 3px;
    background: $navbar-color-black;
    border-radius: 0;
    transition: transform .2s;

    &:first-child {
      transform-origin: top left;
    }
    &:last-child {
      transform-origin: bottom left;
    }
  }

  &:not(.collapsed) {

    .icon-bar:first-child {
      transform: rotate(45deg);
    }
    .icon-bar:nth-child(3n + 2) {
      opacity: 0;
    }
    .icon-bar:last-child {
      transform: rotate(-45deg);
    }

  }
}

.navbar-logo {
  height: $header-height;
  &:hover {
    border: none;
  }
  > img {
    height: 100%;
    width: auto;
    padding: 3px 0;
  }

}

.navbar-collapse {
  background: $color-menu-mobile;
  border-bottom: 3px solid darken($color-menu-mobile, 10%);
}

@media (min-width: $screen-sm-min) {
  .navbar-collapse {
    background: transparent;
    border-bottom: none;
  }
}

.navbar-info {
  display: inline-block;
  padding: 6px 20px 0;
  color: $navbar-color-black;
}

.navbar-family {
  font-weight: 800;
  font-size: .9rem;
}

.navbar-time {
  font-size: .7rem;
}

.navbar-hour {
  font-weight: 600;
}

.navbar-date {
  font-weight: 200;
}
